//1w
export function formatNum(num){
    let newNum=0
    if(num>100000000){
        // 
        newNum = (num/100000000).toFixed(2)
        return newNum + ""
    }else if(num>10000){
        // 
        newNum = (num/10000).toFixed(1)
        return newNum + ""
    }else return num
}

export default{
    install(Vue){
        Vue.filter('formatNum',formatNum)
    }
}